import { Injectable } from '@angular/core';
import { Storage } from '@ionic/storage-angular';

@Injectable({
  providedIn: 'root'
})
export class StorageService {

  constructor(private storage: Storage) { }

  async storeValue(storageKey: string, value: any) {
    const encryptedValue = btoa(escape(JSON.stringify(value)));
    return await this.storage.set(storageKey, encryptedValue);
  }

  async getStoredValue(storageKey: string) {
    return new Promise(resolve => {
      this.storage.get(storageKey).then((value) => {
        if (!value) {
          resolve(false);
        } else {
          resolve(JSON.parse(unescape(atob(value))));
        }
      });
    });
  }

  async removeStoredItem(storageKey: string) {
    await this.storage.remove(storageKey);
  }

  async clearStorage() {
    await this.storage.clear();
  }
}
