import { Injectable } from '@angular/core';
import {environment} from '../environments/environment';
import {HttpClient, HttpHeaders} from '@angular/common/http';
import {ToastController} from '@ionic/angular';
import * as moment from "moment";
import {tap, switchMap, map} from 'rxjs/operators';
import {SharedServiceService} from './shared-service.service';
import {Router} from '@angular/router';
import {StorageService} from './shared-components/storage.service';
import { BehaviorSubject, from, Observable, of } from 'rxjs';
const ACCESS_TOKEN_KEY = 'my-access-token';
const REFRESH_TOKEN_KEY = 'my-refresh-token';
const USER_INFO = 'user-info';

@Injectable({
  providedIn: 'root'
})
export class ApiService {

  // Init with null to filter out the first value in a guard!
  isAuthenticated: BehaviorSubject<boolean> = new BehaviorSubject<boolean>(null);
  currentAccessToken = null;
  authUrl = environment.baseAuthUrl;
  userSubject: BehaviorSubject<any>;

  baseURL: string = environment.baseURLAdminAPIs;
  header: any;
  private country = new BehaviorSubject('');
  constructor(private storageService: StorageService, private router: Router,
              private sharedService: SharedServiceService,
              private http: HttpClient, public toastController: ToastController) {
    this.userSubject = new BehaviorSubject<any>(null);
    this.loadToken();
  }

  loginWithOtp(phoneData): Observable<any> {
    return this.http.post(`${this.authUrl}loginWithOtp`, phoneData).pipe(map((res: any) => {
      this.userSubject.next(res.data.user);
      return res;
    }));
  }

  // Sign in a user and store access and refres token
  verifyOtp(credentials: any): Observable<any> {
    return this.http.post(`${this.authUrl}verifyOtp`, credentials).pipe(
        tap((response) => {
          this.storageService.storeValue(USER_INFO, response.data.user);
          this.storageService.storeValue(ACCESS_TOKEN_KEY, response.data.tokens.access.token);
          this.storageService.storeValue(REFRESH_TOKEN_KEY, response.data.tokens.refresh.token);
          this.isAuthenticated.next(true);
        })
    );
  }

  // Load accessToken on startup
  async loadToken() {
    const token: any = await this.storageService.getStoredValue(ACCESS_TOKEN_KEY);
    if (token) {
      this.currentAccessToken = token;
      this.isAuthenticated.next(true);
      const userDetails = await this.storageService.getStoredValue(USER_INFO);
      this.userSubject.next(userDetails);
    } else {
      this.isAuthenticated.next(false);
      this.userSubject.next(null);
    }
  }

  async logout() {
    const token = await this.storageService.getStoredValue(REFRESH_TOKEN_KEY);
    return this.http.post(`${this.authUrl}logout`, {refreshToken: token}).pipe(
        tap(_ => {
          this.isAuthenticated.next(false);
          this.storageService.removeStoredItem(REFRESH_TOKEN_KEY);
          this.storageService.removeStoredItem(ACCESS_TOKEN_KEY);
        })
    ).subscribe(() => {
      this.isAuthenticated.next(false);
      this.storageService.removeStoredItem(REFRESH_TOKEN_KEY);
      this.storageService.removeStoredItem(ACCESS_TOKEN_KEY);
      this.router.navigate(['/login']);
    });
  }

  // Load the refresh token from storage
  // then attach it as the header for one specific API call
  getNewAccessToken() {
    const refreshToken = from(this.storageService.getStoredValue(REFRESH_TOKEN_KEY));
    return refreshToken.pipe(
        switchMap(token => {
          if (token) {
            const resBody = {
              refreshToken: token
            };
            return this.http.post(`${this.authUrl}refresh-tokens`, resBody);
          } else {
            // No stored refresh token
            return of(null);
          }
        })
    );
  }

  // Store a new access token
  storeAccessToken(accessToken) {
    this.currentAccessToken = accessToken;
    return from(this.storageService.storeValue(ACCESS_TOKEN_KEY, accessToken));
  }

  updateUser(data, userId) {
    return this.http.put(`${this.baseURL}user/${userId}`, data, {});
  }

  getUserDetails() {
    return this.http.get(`${this.baseURL}user`, {});
  }

  getAllServiceTypes() {
    return this.http.get(`${this.baseURL}serviceTypes`, {});
  }
  getAllServices() {
    return this.http.get(`${this.baseURL}services`, {});
  }

  // -------------------------  JSON APIS ------------------------------

  getUserRole() {
    return localStorage.getItem('role');
  }

  setHeaderToken() {
    const token = localStorage.getItem('token');
    this.header = new HttpHeaders({Authorization: `Bearer ${token}`});
  }

  getServicesData() {
    const localUrl = 'assets/json_files/services.json';
    return this.http.get(localUrl);
  }

  getMainServiceData() {
    const localUrl = 'assets/json_files/MainServices.json';
    return this.http.get(localUrl);
  }

  getCombos() {
    const localUrl = 'assets/json_files/packages.json';
    return this.http.get(localUrl);
  }

  getCoupons() {
    const localUrl = 'assets/json_files/coupons.json';
    return this.http.get(localUrl);
  }

  getServicePackage() {
    const localUrl = 'assets/json_files/service-packages.json';
    return this.http.get(localUrl);
  }

  getProducts() {
    const localUrl = 'assets/json_files/products.json';
    return this.http.get(localUrl);
  }

  getBookings() {
    const localUrl = 'assets/json_files/bookings.json';
    return this.http.get(localUrl);
  }

  commonError(err) {
    const errCode = err.status;
    let errorMessage = err.error.message;
    switch (errCode) {
      case 401: {
        this.sharedService.removeAllUserDetails();
        this.router.navigate(['login']);
        break;
      }
      case 0: {
        errorMessage = 'No Internet Connection';
        break;
      }
    }
    this.presentToast(errorMessage).then();
  }

  async presentToast(displayMessage) {
    const toast = await this.toastController.create({
      message: displayMessage,
      position: 'top',
      cssClass: 'ionic-error-toast',
      duration: 2000,
    });
    toast.present();
  }

  private setSession(authResult) {
    if (authResult.data.id) {
      this.sharedService.setUserId(authResult.data.id);
    }
  }

  private setToken(authResult) {
    if (authResult.data.token) {
      this.sharedService.setToken(authResult.data.token);
    }
  }
  //
  // logout() {
  //   localStorage.removeItem("id_token");
  //   localStorage.removeItem("expires_at");
  // }

  public isLoggedIn() {
    return moment().isBefore(this.getExpiration());
  }

  isLoggedOut() {
    return !this.isLoggedIn();
  }

  getExpiration() {
    const expiration = localStorage.getItem("expires_at");
    const expiresAt = JSON.parse(expiration);
    return moment(expiresAt);
  }


}
