import {Injectable} from '@angular/core';
import {CanActivate, Router} from '@angular/router';
import {SharedServiceService} from '../../shared-service.service';
import {Observable} from 'rxjs';
import {ApiService} from '../../api.service';
import {filter, map, take} from 'rxjs/operators';

@Injectable()
export class LoginGuard implements CanActivate {
  constructor(private apiService: ApiService, private router: Router, private sharedService: SharedServiceService) {
  }
  canActivate(): Observable<boolean> {
    return this.apiService.isAuthenticated.pipe(
        filter(val => val !== null), // Filter out initial Behaviour subject value
        take(1), // Otherwise the Observable doesn't complete!
        map(isAuthenticated => {
          if (isAuthenticated) {
            return true;
          } else {
            this.router.navigate(['/login']);
            return false;
          }
        })
    );
  }
}

@Injectable()
export class PreLogin implements CanActivate {
  constructor(private apiService: ApiService, private router: Router, private sharedService: SharedServiceService) {
  }
  canActivate(): Observable<boolean> {
    return this.apiService.isAuthenticated.pipe(
        filter(val => val !== null), // Filter out initial Behaviour subject value
        take(1), // Otherwise the Observable doesn't complete!
        map(isAuthenticated => {
          if (isAuthenticated) {
            this.router.navigate(['/feed']);
            return false;
          } else {
            return true;
          }
        })
    );
  }
}

@Injectable()
export class SignUpGuard implements CanActivate {
  constructor(private router: Router, private sharedService: SharedServiceService) {
  }

  canActivate() {
    if (!this.sharedService.isLoggedIn()) {
      return true;
    } else {
      this.sharedService.presentToast('SuccessFully Logged in!', 'success').then(
          () => {
            this.sharedService.showSpinner.next(true);
            setTimeout(() => {
              this.sharedService.showSpinner.next(false);
            }, 2000);
          }
      );
      this.redirectToUser();
    }
  }

  redirectToUser() {
    this.router.navigate(['/feed']);
  }
}
